@import url('https://fonts.googleapis.com/css?family=Alfa+Slab+One|Patua+One&display=swap');

.projector_container
{
	width: 100%;
	padding:40px 0;
	box-sizing: border-box;
	margin:1px 0px;
	/*background-image: url('./bak.svg');*/
	background-image: linear-gradient(-90deg, rgba(116, 197, 197, 0.877), rgba(116, 197, 197, 0.877) , url('./code_women.jpg' ));
	background-image:linear-gradient(to bottom, rgba(0, 165, 165, 0.575), rgba(26, 116, 116, 0.575)),url('besto_1000.jpg');
	background-size: cover;
	background-position : center top;
    background-repeat: no-repeat;
}
.projecter_wrapper
{
	max-width:600px;
    display: flex;
	flex-direction: row;
	flex-direction: column;
	border-radius: 4px;
	overflow: hidden;
	margin : 0px;
	margin-left:10px;
	/* background-color:#74b4c2a4; */
}
.poster_container {
	max-width: 600px;
	height: max-content;
	padding:60px 30px;
	box-sizing: border-box;
}


.poster_text_wrapper h1 {
	font-size: 3.2rem;
	font-family: 'Alfa Slab One', cursive;
	margin: 20px 0px;
	color: #54c0d7;
	color: white;
	text-align: left;
	font-weight: 300;
}

.poster_text_wrapper h2 {
	font-size: 2.9rem;
	/*font-family: 'Patua One', cursive;*/
	font-family: 'Alfa Slab One', cursive;
	margin: 0px;
	color:#0e232e;
	text-align: left;
	font-weight:200;
}


@media screen and (max-width: 760px) {

	.projector_container
	{
		background-image:linear-gradient(to bottom, rgba(0, 165, 165, 0.575), rgba(26, 116, 116, 0.575)),url('besto_500.jpg');
		height : auto;
		padding:5px;
		padding-bottom:40px;
		margin:0px;
	}
		

	.poster_text_wrapper h1 {
		font-size: 2.5rem;
		margin: 10px 0px;
	}

	.poster_text_wrapper h2 {
		font-size: 2.3rem;
		margin: 0px;
	}

	.poster_container {
		max-width: 540px;
		padding:10px 30px;
		margin-top: 20px;
	}
	
}



@media screen and (max-width: 460px) {	
	.projector_container {
		background-image:linear-gradient(to bottom, rgba(0, 165, 165, 0.575), rgba(26, 116, 116, 0.575)),url('besto_1000.jpg');
	}
	.projector_slider_area {
		margin : 0px;
	}
	.projector_slider_area img 
	{
		width:100%;
		height:300px;
		padding:0px;
		border: 0px;
		margin: 0px;
	}

	.poster_text_wrapper h1 {
		font-size: 2.1rem;
		margin: 10px 0px;
	}

	.poster_text_wrapper h2 {
		font-size: 2rem;
		margin: 0px;
	}

	.poster_container {
		padding:10px;
		margin-top: 20px;
	}
	
}